import AbstractView from '../framework/view/abstract-view.js';
import { humanizeEventDueDate, capitalizeFirstLetter, formatDateDifference, getOffersByType, getDestinationById } from '../utils/event.js';

function createEventsItemTemplate(event, destination, offersByType) {
  const {type, dateFrom, dateTo, basePrice, isFavorite} = event;
  const eventOffers = offersByType.filter((offerType) => event.offers.includes(offerType.id));

  return (
    `<li class="trip-events__item">
      <div class="event">
        <time class="event__date" datetime="${humanizeEventDueDate(dateFrom, 'YYYY-MM-DD')}">${humanizeEventDueDate(dateFrom, 'MMM D')}</time>
        <div class="event__type">
          <img class="event__type-icon" width="42" height="42" src="img/icons/${type}.png" alt="Event type icon">
        </div>
        <h3 class="event__title">${capitalizeFirstLetter(type)} ${destination.name}</h3>
        <div class="event__schedule">
          <p class="event__time">
            <time class="event__start-time" datetime="${dateFrom}">${humanizeEventDueDate(dateFrom, 'HH:mm')}</time>
            &mdash;
            <time class="event__end-time" datetime="${dateTo}">${humanizeEventDueDate(dateTo, 'HH:mm')}</time>
          </p>
          <p class="event__duration">${formatDateDifference(dateFrom, dateTo)}</p>
        </div>
        <p class="event__price">
          &euro;&nbsp;<span class="event__price-value">${basePrice}</span>
        </p>
        <h4 class="visually-hidden">Offers:</h4>
        <ul class="event__selected-offers">
          ${eventOffers.map((offer) =>(
      `<li class="event__offer">
        <span class="event__offer-title">${offer.title}</span>
        &plus;&euro;&nbsp;
        <span class="event__offer-price">${offer.price}</span>
      </li>`)).join('')}
        </ul>
        <button class="event__favorite-btn ${isFavorite ? 'event__favorite-btn--active' : ''}" type="button">
          <span class="visually-hidden">Add to favorite</span>
          <svg class="event__favorite-icon" width="28" height="28" viewBox="0 0 28 28">
            <path d="M14 21l-8.22899 4.3262 1.57159-9.1631L.685209 9.67376 9.8855 8.33688 14 0l4.1145 8.33688 9.2003 1.33688-6.6574 6.48934 1.5716 9.1631L14 21z"/>
          </svg>
        </button>
        <button class="event__rollup-btn" type="button">
          <span class="visually-hidden">Open event</span>
        </button>
      </div>
    </li>`
  );
}

export default class EventsItemView extends AbstractView {
  #event = null;
  #destination = null;
  #offersByType = null;
  #allDestinations = [];
  #allOffers = [];
  #handleEditClick = null;

  constructor({event, allDestinations, allOffers, onEditClick}) {
    super();
    this.#event = event;
    this.#allDestinations = allDestinations;
    this.#allOffers = allOffers;
    this.#destination = getDestinationById(this.#allDestinations, this.#event.destination);
    this.#offersByType = getOffersByType(this.#allOffers, this.#event.type);
    this.#handleEditClick = onEditClick;

    this.element.querySelector('.event__rollup-btn').addEventListener('click', this.#editClickHandler);
  }

  get template() {
    return createEventsItemTemplate(this.#event, this.#destination, this.#offersByType);
  }

  #editClickHandler = (evt) => {
    evt.preventDefault();
    this.#handleEditClick();
  };
}
